.columns {
  color: #ffffff;
  background-color: #008662;
  height: 44.88px;
  font-weight: 300;
  font-size: 12px;
  text-transform: uppercase;
  text-indent: 20px;
  position: relative;
}

.columns2 {
  width: 200px;
}

.columnID {
  width: 100px;
}

.columns:first-child {
  border-top-left-radius: 6px;
}

.columns:last-child {
  border-top-right-radius: 6px;
}

.filterIcons {
  width: 10px !important;
  height: auto !important;
}

.filterIcons,
.clearFilterIcons {
  position: relative;
  margin-bottom: 2px;
  cursor: pointer;
}

.clearFilterIcons {
  filter: brightness(0) invert(1);
  width: 12.46px;
  height: 15px;
  margin-left: 10px;
}

.dropdownFirstname,
.dropdownLastname,
.dropdownEmail,
.dropdownRole,
.dropdownDateCreated,
.dropdownLastSeen {
  position: absolute;
  background-color: #ffffff;
  color: #000000;
  bottom: 0;
  margin-left: 25px;
  border-radius: 4px;
  border: 1px solid #b1b1b1;
  z-index: 1;
  box-shadow: 0px 3px 5px #c5c5c5;
}

.dropdownFirstname,
.dropdownLastname,
.dropdownEmail {
  width: 232px;
  height: 180px;
  margin-bottom: -182px;
}

.dropdownRole {
  width: 175px;
  height: 190px;
  margin-bottom: -167px;
}

.dropdownDateCreated,
.dropdownLastSeen {
  right: 0;
  margin-right: 0px;
  height: 298px;
  width: 850px;
  margin-bottom: -300px;
  padding-top: 20px;
  padding-bottom: 20px;
}

.icon {
  position: relative;
  width: 20px;
}

.icon img {
  position: absolute;
  left: 0;
  margin-top: 2px;
}

.icon img {
  margin-left: 5px;
}

.label {
  max-width: 100%;
  overflow-x: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  cursor: pointer;
}


.dropdownValidityDate {
  position: absolute;
  background-color: #ffffff;
  color: #000000;
  bottom: 0;
  margin-left: 25px;
  border-radius: 4px;
  border: 1px solid #b1b1b1;
  z-index: 1;
  box-shadow: 0px 3px 5px #c5c5c5;
}

.dropdownValidityDate {
  margin-right: 0px;
  width: 850px;
  margin-bottom: -300px;
  padding-top: 10px;
  padding-bottom: 20px;
  top: 46px;
}

.numberDiv{
  float: right;
  display: flex;
}
.noIcon {
  width: 0px !important;
}